import React, { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";
import Style from "./style";
import Logo from "../../assets/image/header-logo.png";
import { Button, Modal } from "antd";
import CloseIcon from "../../assets/image/close-icon.png";
import EmailEnvelop from "../../assets/image/email-evelop.png";

const Header = (props) => {
  const [contactPopup, setContactPopup] = useState(false);

  return (
    <Style>
      <div className="header">
        <div className="logo">
          <NavLink to="/">
            <img src={Logo} />
          </NavLink>
        </div>
        <div className="navbar">
          {props?.showHome ? <NavLink to="/">Home</NavLink> : null}
          <NavLink to="/access-genie">Access Genie</NavLink>
          <NavLink to="/testimonials">Employee Testimonials</NavLink>
          <Button type="primary" onClick={() => setContactPopup(true)}>
            CONTACT US
          </Button>
        </div>
      </div>

      <Modal
        centered
        open={contactPopup}
        closeIcon={<img src={CloseIcon} style={{ width: "100%" }} />}
        footer={false}
        onOk={() => setContactPopup(false)}
        onCancel={() => setContactPopup(false)}
      >
        <Style>
          <div className="contact-us-message">
            <img src={EmailEnvelop} />
            <h3 className="head-label">Get in touch</h3>
            <p>Incase you need additional information, please write to us:</p>
            <strong>ace.europe@indegene.com</strong>
          </div>
        </Style>
      </Modal>
    </Style>
  );
};

export default Header;
