import React, { Suspense } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Layout from "./components/Layout";
import PageLoader from "./components/PageLoader";
import "./App.css";

const Home = React.lazy(() => import("./components/Home"));
const AccessGenie = React.lazy(() => import("./components/AccessGenie"));
const RegionalizedCapabilities = React.lazy(() =>
  import("./components/RegionalizedCapabilities")
);
const MarketIntelligence = React.lazy(() =>
  import("./components/MarketIntelligence")
);
const Testimonials = React.lazy(() => import("./components/Testimonial"));

const basename =
  process.env.NODE_ENV === "production" ? process.env.REACT_APP_BASE_URL : "";

function App() {
  return (
    <Router {...(basename ? { basename } : {})}>
      <Layout>
        <Suspense fallback={<PageLoader />}>
          <Routes>
            <Route exact path="/" element={<Home />} />
            <Route exact path="/access-genie" element={<AccessGenie />} />
            <Route
              exact
              path="/regionalized-capabilities"
              element={<RegionalizedCapabilities />}
            />
            <Route
              exact
              path="/market-intelligence"
              element={<MarketIntelligence />}
            />
            <Route exact path="/testimonials" element={<Testimonials />} />
          </Routes>
        </Suspense>
      </Layout>
    </Router>
  );
}

export default App;
